        </div>
        <div class="investment_content">
          <div class="content_title mb50">资金团队</div>
          <div>
            <Table id="table1" :columns="columns1" :data="data.group"></Table>
          </div>
        </div>
        <div class="investment_content">
          <div class="content_title mb50">投资案例</div>
          <div>
