            <img v-else-if="data.type ==2" src="../../assets/img/detail/t2.png" alt />
            <img v-else-if="data.type ==3" src="../../assets/img/detail/t3.png" alt />
          </div>
          <div class="userInfoBox_certification">
            认证类型：
            <span
              class="txt_box"
            >{{data.type== 1 ? '个人认证' : data.type== 2 ? '企业认证' : data.type== 3 ? '政府认证' : '无' }}</span>
          </div>
          <div
            class="userInfoBox_certification"
          >认证主体：{{data.type== 1 ? data.auditingName : data.type== 2 ? data.company.companyName : data.type== 3 ? data.government.unitsName : '无' }}</div>
          <div
