        <li v-for="(item, index) in projectData" :key="index">
          <p>
            <img :src="imgUrl+item.logo" alt />
          </p>
          <p style="font-size:18px;">项目名称:{{item.name}}</p>
          <p style="font-size:16px;color:#adadad;">联系人:{{item.contactName}}</p>
          <p style="font-size:16px;color:#adadad;">联系方式:{{item.contactPhone}}</p>
          <br />
          <p>
            <Poptip
              style="text-align: left;"
              class="flR"
              confirm
              title="是否投递此项目,请确认?"
              @on-ok="clickprojectDeliveryAdd(item.code)"
            >
              <Button class="flR">投递此项目</Button>
            </Poptip>
          </p>
