<script>
import { Button, Icon, Table, Affix, Drawer, Poptip } from "iview";
import { getMoney } from "../../api/capital";
import { messages, imgUrl } from "../../utils";
import { projects } from "../../api/project";
import { collect, cancelCollect, projectDeliveryAdd } from "../../api/public";
// 引入基本模板
import echarts from "echarts/lib/echarts";
// 再引入你需要使用的图表类型
import "echarts/lib/chart/bar";
import "echarts/lib/chart/line";
// 引入提示框和title组件，图例
import "echarts/lib/component/title";
import "echarts/lib/component/tooltip";
import "echarts/lib/component/legend";
export default {
  components: {
    [Button.name]: Button,
    [Table.name]: Table,
    [Affix.name]: Affix,
    [Drawer.name]: Drawer,
    [Poptip.name]: Poptip,
    [Icon.name]: Icon
  },
  data() {
    return {
      imgUrl: imgUrl,
      show: false,
      active: 0,
      data: {},
      // 用户名下所有项目
      projectData: [],
      menusList: ["资金介绍", "投资概况", "图表分析"],
      columns1: [
        {
          title: "姓名",
          key: "name",
          width: 200
        },
        {
          title: "职位",
          key: "position",
          width: 200
        },
        {
          title: "介绍",
          key: "introduce"
        }
      ],
      columns2: [
        {
          title: "时间",
          key: "time"
        },
        {
          title: "项目名称",
          key: "projectName",
          width: 400
        },
        {
          title: "金额",
          slot: "money"
        }
      ]
    };
  },
  methods: {
    clickActive(_index) {
      this.active = _index;
      var scrollNub =
        document.getElementsByClassName("block1")[_index].offsetTop - 40;
      document.documentElement.scrollTop = scrollNub;
    },
    //添加收藏
    clickCollect() {
      collect({ code: this.data.code, type: 2 }).then(res => {
        if (res.data.status == "SUCCESS") {
          messages.success("收藏成功");
          this.data.isColler = "已收藏";
        }
      });
    },
    //取消收藏
    clickCancelCollect() {
      cancelCollect({ code: this.data.code, type: 2 }).then(res => {
        if (res.data.status == "SUCCESS") {
          messages.error("取消收藏");
          this.data.isColler = "未收藏";
        }
      });
    },
    // 获取用户项目
    getProjects() {
      projects({ name: "", pageNo: 1, status: 1, type: -1 }).then(res => {
        if (res.data.status == "SUCCESS") {
          this.show = true;
          this.projectData = res.data.data.rows;
        }
      });
    },
    /**
     * @function 我要投递
     * @param {projectCode 项目code}
     */
    clickprojectDeliveryAdd(projectCode) {
      projectDeliveryAdd({ moneyCode: this.data.code, projectCode }).then(
        res => {
          if (res.data.status == "SUCCESS") {
            messages.success(`项目投递成功`);
          }
        }
      );
    },
    getData() {
      //获取项目详情
      getMoney({ id: this.$route.params.id }).then(res => {
        if (res.data.status == "SUCCESS") {
          this.data = res.data.data;
          this.data.territory = this.data.territory.split(",");
          this.data.createTime = this.data.createTime.split(" ")[0];
          this.data.address = JSON.parse(this.data.address).name;

          // 基于准备好的dom，初始化echarts实例
          echarts.init(document.getElementById("main1")).setOption({
            animationEasing: "bounceOut",
            title: {
              text: "年度投资金额 单位：万元"
            },
            tooltip: {},
            xAxis: {
              data: this.data.statistical.xAxis
            },
            grid: {
              left: "3%",
              right: "4%",
              bottom: "3%",
              containLabel: true
            },
            yAxis: {},
            series: [
              {
                name: "每月融资",
                type: "bar",
                data: this.data.statistical.series.investment,
                itemStyle: {
                  normal: {
                    color: "#2d8df0", //设置柱子颜色
                    label: {
                      show: true, //柱子上显示值
                      position: "top", //值在柱子上方显示
                      textStyle: {
                        color: "#000000" //值得颜色
                      }
                    }
                  }
                }
              }
            ]
          });
          // 基于准备好的dom，初始化echarts实例
          echarts.init(document.getElementById("main2")).setOption({
            title: {
              text: "资金月活跃度"
            },
            tooltip: {
              trigger: "axis"
            },
            legend: {
              data: ["收到项目投递次数", "成功投资次数", "拒绝投资次数"]
            },
            grid: {
              left: "3%",
              right: "4%",
              bottom: "3%",
              containLabel: true
            },
            xAxis: {
              type: "category",
              boundaryGap: false,
              data: this.data.statistical.xAxis
            },
            yAxis: {
              type: "value"
            },
            series: [
              {
                name: "收到项目投递次数",
                type: "line",
                stack: "总量",
                data: this.data.statistical.series.deliveryNumTotal
              },
              {
                name: "成功投资次数",
                type: "line",
                stack: "总量",
                data: this.data.statistical.series.through
              },
              {
                name: "拒绝投资次数",
                type: "line",
                stack: "总量",
                data: this.data.statistical.series.refused
              }
            ]
          });
        }
      });
    },
    //监听滚动
    getScroll() {
      let active = document.documentElement.scrollTop;
      let active2 = document.getElementsByClassName("block1")[2].offsetTop - 40;
      let active1 = document.getElementsByClassName("block1")[1].offsetTop - 40;
      let active0 = document.getElementsByClassName("block1")[0].offsetTop - 40;
      if (active < active1) {
        this.active = 0;
      } else if (active > active1 && active < active2) {
        this.active = 1;
      } else if (active > active2) {
        this.active = 2;
      }
    }
  },
  mounted() {
    this.getData();
    window.addEventListener("scroll", this.getScroll);
  },
  watch: {
    $route(to, from) {
      this.getData();
    }
  },
  destroyed() {
    window.removeEventListener("scroll", this.getScroll);
  }
};
</script>
